import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: 16,
    margin: "30px 0",
    [theme.breakpoints.up('md')]: {
      margin: "70px 60px"
    },
  }
}));

function About(props) {
  const classes = useStyles();
  const getHeight = () => props.windowInnerHeight - 105 + "px";

  return (
    <div style={{ minHeight: getHeight() }}>
      <Typography variant="h4" id="About" paragraph>
        About
      </Typography>
      <Paper className={classes.paper} elevation={8}>
        <Typography paragraph>
        Hi! I am a game developer who is skilled in using Unity, with 5 months of commercial experience and numerous finished non-commercial/student projects. I enjoy improving my skills and taking part in the creation of awesome, engaging games!
        </Typography>
      </Paper>
      <Typography variant="h5" paragraph>
        Skills
      </Typography>
      <Grid container>
        <Grid item xs={12}>
          <Typography variant="h6" paragraph>
            Game engines
          </Typography>
          <Typography paragraph>&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Unity</Typography>
          <Typography paragraph>&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Unreal Engine</Typography>
          <Typography variant="h6" paragraph>
            Programming languages
          </Typography>
          <Typography paragraph>&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; C#</Typography>
          <Typography paragraph>&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; C++</Typography>
          <Typography paragraph>&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Blueprints Visual Scripting</Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="h6" paragraph>
            Languages
          </Typography>
          <Typography paragraph>&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Polish (native)</Typography>
          <Typography paragraph>&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; English</Typography>
          <Typography paragraph>&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Japanese</Typography>
          <Typography variant="h6" paragraph>
            Soft skills
          </Typography>
          <Typography paragraph> &nbsp; Attention to detail</Typography>
          <Typography paragraph> &nbsp; Teamwork</Typography>
          <Typography paragraph> &nbsp; Openness to feedback</Typography>
        </Grid>
      </Grid>
      <br />
    </div>
  );
}

export default About;
