import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { makeProjectId } from "./HelperFunctions";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faApple, faGooglePlay } from "@fortawesome/free-brands-svg-icons";
import WebIcon from '@material-ui/icons/Web';
import { purple, pink } from '@material-ui/core/colors';
import Divider from '@material-ui/core/Divider';
import Box from '@material-ui/core/Box';

const useStyles = makeStyles((theme) => ({
  cardRoot: (props) => ({
    backgroundColor: "#013B66",
    background: `linear-gradient(-45deg, ${pink[900]}, ${purple[900]})`
  }),
  button: {
    backgroundColor: '#191919',
    borderColor: '#191919',
    color: '#CDCBC8',
    '&:hover': {
      backgroundColor: '#292929',
      borderColor: '#292929',
    },
    '&:active': {
      backgroundColor: '#101010',
      borderColor: '#101010',
    }
  },
  cardContent: {
    paddingBottom: 0,
  },
  cardButtons: {
    marginLeft: theme.spacing(1),
    marginBottom: theme.spacing(1),
    flexWrap: "wrap",
  },
  cardButtonsBox: {
    "& > a": {
      marginRight: theme.spacing(1),
      marginBottom: theme.spacing(1),
    }
  }
}));


function Project(props) {
  const classes = useStyles(props);

  const id = makeProjectId(props.projectName);

  return (
    <Card className={classes.cardRoot} id={id} elevation={8}>
      <CardHeader
        title={props.projectName}
        subheader={props.subheader}
      />
      <Divider />
      <CardContent className={classes.cardContent}>
        {props.children}
      </CardContent>
      <CardActions className={classes.cardButtons}>
        <Box className={classes.cardButtonsBox}>
          {props.playStoreUrl ? (
            <Button
              variant="contained"
              className={classes.button}
              size="small"
              startIcon={<FontAwesomeIcon icon={faGooglePlay} />}
              href={props.playStoreUrl}
              target="_blank"
            >
              Google Play
            </Button>
          ) : (
            ""
          )}
          {props.appStoreUrl ? (
            <Button
              variant="contained"
              className={classes.button}
              size="small"
              startIcon={<FontAwesomeIcon icon={faApple} />}
              href={props.appStoreUrl}
              target="_blank"
            >
              App Store
            </Button>
          ) : (
            ""
          )}
          {props.websiteUrl ? (
            <Button
              variant="contained"
              className={classes.button}
              size="small"
              startIcon={<WebIcon/>}
              href={props.websiteUrl}
              target="_blank"
            >
              Game website
            </Button>
          ) : (
            ""
          )}
        </Box>

      </CardActions>
    </Card>
  );
}

export default Project;
