import React from "react";
import Typography from "@material-ui/core/Typography";
import Masonry from "react-masonry-css";
import { makeStyles } from "@material-ui/core";
import CommercialProject from "./CommercialProject";

const useStyles = makeStyles((theme) => ({
  masonryGrid: {
    display: "flex",
    marginLeft: theme.spacing(-4),
    width: "inherit",
    [theme.breakpoints.up('lg')]: {
      paddingLeft: "60px",
      paddingRight: "60px"
    },
  },
  masonryColumn: {
    paddingLeft: theme.spacing(4),
    backgroundClip: "padding-box",
    "& > div": {
      marginBottom: theme.spacing(6)
    }
  }
}));

function Projects(props) {
  const classes = useStyles();

  let projects = [
    {
      project:
        <CommercialProject
          key={1}
          projectName="Hero Legacy"
          subheader="Employed: Apr 2023 &ndash; Aug 2023" // (permanent contract&thinsp;&ndash;&thinsp;laid off due to collective redundancy)
          playStoreUrl="https://play.google.com/store/apps/details?id=com.gamesture.hero.legacy.open.world.adventure.rpg"
          appStoreUrl="https://apps.apple.com/pl/app/hero-legacy/id1563000991"
          websiteUrl="https://herolegacy.com/"
        >
          <Typography paragraph variant="body2" color="textSecondary">A mobile Unity game rooted in client-server architecture.</Typography>
        </CommercialProject>
    },
  ];

  return (
    <React.Fragment>
      <Typography variant="h4" id="Commercial projects" paragraph>
        Commercial projects <Typography color="textSecondary" display="inline" component="span">ProjectsIworkedonforacompany</Typography>
      </Typography>
      <Masonry
        breakpointCols={{ default: 1 }}
        className={classes.masonryGrid}
        columnClassName={classes.masonryColumn}
      >
        {projects.map(p => p.project)}
      </Masonry>
      <br />
    </React.Fragment>
  );
}

export default Projects;
