import React from "react";
import Divider from "@material-ui/core/Divider";
import MonetizationOnSharpIcon from '@material-ui/icons/MonetizationOnSharp';
import CodeIcon from "@material-ui/icons/Code";
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import AlternateEmailIcon from "@material-ui/icons/AlternateEmail";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import GitHubIcon from "@material-ui/icons/GitHub";
import DrawerList from "./DrawerList";
import DrawerListOfLinks from "./DrawerListOfLinks";

function DrawerContent(props) {
  return (
    <div>
      <Divider />
      <DrawerList
        content={[
          {
            text: "Commercial projects",
            icon: <MonetizationOnSharpIcon />
          },
          {
            text: "Noncommercial projects",
            icon: <CodeIcon />
          },
          {
            text: "About",
            icon: <InfoOutlinedIcon />
          },
          {
            text: "Contact",
            icon: <AlternateEmailIcon />
          }
        ]}
        closeDrawer={props.closeDrawer}
      />
      <Divider />
      <DrawerListOfLinks
        content={[
          // {
          //   text: "GitHub",
          //   icon: <GitHubIcon />,
          //   url: "https://github.com/arek-b"
          // },
          {
            text: "LinkedIn",
            icon: <LinkedInIcon />,
            url: "https://www.linkedin.com/in/arkadiusz-bergmann/"
          }
        ]}
      />
    </div>
  );
}

export default DrawerContent;
