import React from "react";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import EmailIcon from "@material-ui/icons/Email";
import FileCopyIcon from '@material-ui/icons/FileCopy';
import CheckIcon from '@material-ui/icons/Check';
import { IconButton } from "@material-ui/core";
import { useState } from 'react';

function Contact(props) {
  const email = "arkadiuszbergmann@gmail.com";
  const [copiedTimeoutId, setCopiedTimeoutId] = useState(0);
  const getHeight = () => props.windowInnerHeight - 125 + "px";
  const copyEmailToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(email);
      clearTimeout(copiedTimeoutId);
      setCopiedTimeoutId(setTimeout(() => setCopiedTimeoutId(0), 3000));
    } catch (err) {
      console.log('Failed to copy');
    }
  }

  return (
    <div style={{ minHeight: getHeight() }}>
      <Typography variant="h4" id="Contact" paragraph>
        Contact
      </Typography>

      <Button
        href={"mailto:" + email}
        startIcon={<EmailIcon />}
        style={{ userSelect: "text" }}
        variant="outlined"
        size="small"
      >
        {email}
      </Button>
      {window.isSecureContext ? 
        <IconButton
          style={{marginLeft: "8px"}}
          onClick={copyEmailToClipboard}
          variant="outlined"
          title="Copy to clipboard"
        >
          {copiedTimeoutId > 0 ? <CheckIcon/> : <FileCopyIcon/>}
        </IconButton>
      : "" }
    </div>
  );
}

export default Contact;
