import React, { useState } from "react";
import Typography from "@material-ui/core/Typography";
import Project from "./Project";
import { red, blue, green, grey } from "@material-ui/core/colors";
import Masonry from "react-masonry-css";
// import Link from '@material-ui/core/Link';
// import { scrollTo, makeProjectId } from "./HelperFunctions";
// import Stack from '@mui/material/Stack';
// icons
// import SortIcon from '@material-ui/icons/Sort';
// radio
// import Radio from '@material-ui/core/Radio';
// import RadioGroup from '@material-ui/core/RadioGroup';
// import FormControlLabel from '@material-ui/core/FormControlLabel';
// import FormControl from '@material-ui/core/FormControl';

// game icons
import icon_cmf from "./img/icon_cmf.png";
import icon_alien_rush from "./img/icon_alien_rush.png";
import icon_ld48 from "./img/icon_ld48.png";
