import arduino1 from "./img/arduino1.png";
import ss_covert1 from "./img/ss_covert.jpg";
import ss_covert2 from "./img/ss_covert2.jpg";
import ss_covert3 from "./img/ss_covert3.jpg";
import ss_covert4 from "./img/ss_covert4.jpg";
import ss_alien_rush from "./img/ss_alien_rush.jpg";
import ss_ld48 from "./img/ss_ld48.jpg";
import ss_element_hunt from "./img/ss_element_hunt.png";
import final_road1 from "./img/final_road1.jpg";
import final_road2 from "./img/final_road2.jpg";
import rustbandits1 from "./img/ss_rustbandits1.jpg";
import rustbandits2 from "./img/ss_rustbandits2.jpg";
import { makeStyles } from "@material-ui/core";

//videos
